.contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 26px;
}
.contacts__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  grid-column: 1 / 2;
  margin-top: 0;
}

.contacts__address {
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
  grid-column: 2 / 3;
}

.contacts__map {
  grid-column: 1 / 3;
  display: block;
  background-color: #77f4b6;
  width: 100%;
}
