.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin-top: 5px;
  margin-bottom: 42px;
}

.advantages__item {
  color: #444444;
  padding-left: 80px;
  margin-bottom: 26px;

  @media (min-width: $tablet-width) {
    max-width: 230px;
    padding-top: 151px;
    padding-bottom: 57px;
    padding-left: 0;
    font-size: 16px;
    line-height: 24px;

  }

}

.advantages__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 153px;
    row-gap: 37px;

  }

  @media (min-width: $desktop-width) {
    gap: 94px;

  }
}
